@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lato:wght@400;700;900&family=Lora:wght@400;500;600&family=Martel:wght@400;600;700&family=Mukta:wght@300;400;500;600;700&family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800&display=swap);
@font-face {
  font-family: 'GmarketSans';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff') format('woff');
  font-weight: lighter;
  font-style: lighter;
}
@font-face {
  font-family: 'GmarketSans';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  font-family: "Poppins",'Noto Sans KR', NanumGothic, roboto, sans-serif;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block;
}
div, span, article, section, header, footer, aside, p, ul, li, fieldset, legend, label, a, nav, form {
	box-sizing: border-box;
}
ol, ul, li {
	list-style: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
img {
	max-width: 100%;
	height: auto;
	border: 0;
}
button {
	border: 0;
	background: transparent;
	cursor: pointer;
}

a{
	text-decoration: none;
	color: black;
}


#wrap.mobile{
    position: relative;
    width: 100%; max-width: 1024px;}
#wrap #main {
      width: 100%;
}
  
  
#wrap.mobile #header{
  z-index: 100;
  position: fixed;
  max-width: 1024px;
  width: 100%; top: 0;
  padding: 20px 0px 10px 0px;
  background: #fff;
}
#header.active{
  padding: 0;
}

#header .header-wrap .header-inner{
height: 50px;
}
#header .header-wrap .header-inner .logo {
  text-align: left;
  padding-left: 20px;
}

#header .header-wrap .header-inner .logo a{
  color:#000;
  font-size: 24px;
}

#header .header-wrap .header-inner .navButton{
  width: 36px; height: 36px; 
  position: absolute;
  right: 24px; top: 20px;
}



#modal-menu .navButton{
  width: 36px; height: 36px; 
  position: absolute;
  right: 24px; top: 20px;
}


.navButton span{
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  border-radius: 2px;
  background: #000;
  transition: 0.3s;
}
.navButton span:nth-child(1){top: 0;}
.navButton span:nth-child(2){top: 50%; transform: translateY(-50%);}
.navButton span:nth-child(3){top: 50%; transform: translateY(-50%);}
.navButton span:nth-child(4){bottom: 0;}

.navButton.open span:nth-child(1){opacity: 0;}
.navButton.open span:nth-child(2){transform: translateY(-50%) rotate(45deg);}
.navButton.open span:nth-child(3){transform: translateY(-50%) rotate(-45deg);}
.navButton.open span:nth-child(4){opacity: 0;}

#modal-menu{
  display: none;
  position: fixed;
  z-index: 200;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,1);
}
.modal-inner{
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#modal-menu .modal-inner .navButton span{
  background: #fff;
}

#modal-menu .modal-inner .gnb{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 250px;
}

#modal-menu .modal-inner .gnb li{
  padding-bottom: 16px;
  width: 100%;
  text-align: center;
}

#modal-menu .modal-inner .gnb li a{
  color: white;
  text-transform: capitalize;
  font-size: 24px;
}
#modal-menu .modal-inner .sub-gnb{
  margin-top: 16px;
}
#modal-menu .modal-inner .sub-gnb li{
  padding-bottom: 12px;
}

#modal-menu .modal-inner .sub-gnb li a{
  color: white;
  font-size: 16px;
  opacity: 0.8;
}

@media screen and (max-width:800px){

}


@media screen and (max-width:660px){
  #header .header-wrap .header-inner #menubtn{width: 30px; height: 30px;}
}

@media screen and (max-width:480px){
  #header{
    padding-top: 10px;
  }
  #header .header-wrap .header-inner .logo{
    padding-left: 16px;
  }
  #modal-menu .navButton{
    right: 16px; top: 16px;
  }
  #header .header-wrap .header-inner .navButton{
    right: 16px; top: 16px;
  }
  
}
#wrap.pc #header{
    z-index: 100;
    position: fixed;
    width: 100%; top: 0; left: 0; right: 0;
    padding: 20px 3% 10px 4.948%;
    position: relative;
    
  }

#header .header-wrap .header-inner{
  height: 50px;
  display: flex;
}

#header .header-wrap .header-inner nav{
  margin-left: auto;
  width: 57%;
}

#header .header-wrap .header-inner .gnb{
  display: flex;
}

#header.open .header-wrap .header-inner .subnav-bg{
  height: 140px;
}

#header .header-wrap .header-inner .gnb > li{
  margin: 0 18px;
  flex: 1 1;
  position: relative;
  text-align: center;
  font-size: 16px;
  transition: 0.4s;
  transform: scale(1.0);
}

#header .header-wrap .header-inner .gnb > li.active{
  transform: scale(1.2);
  font-weight: 500;
}
#wrap.pc{
    width: 100%;
    min-width: 1024px;
}
